import React, { useState, useCallback } from 'react'
import './App.css';
import Lists from './components/Lists';
import Form from './components/Form';

const initialTodoData = localStorage.getItem("todoData") ? JSON.parse(localStorage.getItem("todoData")) : [];

export default function App() {


  //   ,         state 
  const [todoData, setTodoData] = useState(initialTodoData);

  const [value, setValue] = useState(""); //   


  const handleSubmit = (e) => {  //     
    e.preventDefault();

    let newTodo = {
      id: Date.now(),
      title: value,
      completed: false,
    };

    setTodoData((prev) => [...prev, newTodo])  // , value     
    localStorage.setItem('todoData',JSON.stringify([...todoData,newTodo]));
    setValue("");
  }


  const handleClick = useCallback((id) => {   // useCallback   
    let newTodoData = todoData.filter((data) => data.id !== id);
    setTodoData(newTodoData)
    localStorage.setItem('todoData',JSON.stringify(newTodoData));
  }, [todoData]);

  const handleRemoveClick = () => {
    setTodoData([]);
    localStorage.setItem('todoData',JSON.stringify([]));
  }

  return (
    <div className='flex items-center justify-center w-screen h-screen bg-blue-100'>
      <div className='w-full p-6 m-4 bg-white rounded shadow md:w-3/4 md:max-w-lg lg:w-3/4 lg:max-w-lg'>
        <div className='flex justify-between mb-3'>
          <h1>  </h1>
          <button
            className='p-2 text-red-500 border-2 border-red-400 rounded
                          hover:text-white hover:bg-red-200'
            onClick={handleRemoveClick}>Delete All</button>
        </div>

        <Lists todoData={todoData} setTodoData={setTodoData} handleClick={handleClick} />

        <Form handleSubmit={handleSubmit} value={value} setValue={setValue} />

      </div>
    </div>
  )
}
